import React, {Component} from 'react';
import './App.css';

import HomePage from './pages/HomePage';
import CraftingPage from './pages/CraftingPage';
import GatheringPage from './pages/GatheringPage';
import FishingPage from './pages/FishingPage';
import NavBar from './NavBar';

class App extends Component {
  constructor() {
    super();
    this.state = {
      isLoading: true,
      searchString: '',
      searchResults: [],
      selectedObj: [],
      gatheringItemResults: [],
      craftingItemResults: [],
      fishingItemResults: [],
      lastIndex: 0,
      mapData: [],
      fishingSpotData: [],
      gatheringPointData: []
    };
    this.updateSearchTerm = this.updateSearchTerm.bind(this);
    this.performSearch = this.performSearch.bind(this);
    this.updateSelectedObj = this.updateSelectedObj.bind(this);
    this.lookupItem = this.lookupItem.bind(this);
    this.lookupRecipe = this.lookupRecipe.bind(this);
  }

  componentDidMount() {
    const mapData = [];

    fetch('./MappyData.json', 
      {
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      })
      .then(response => response.json())
      .then(results => results.map(item => {
        mapData.push(item);
        return item;
    }));

    const fishingSpotData = [];
    const urlFishing = 'https://xivapi.com/fishingspot?limit=1000&columns=ID,Item0TargetID,Item1TargetID,Item2TargetID,Item3TargetID,Item4TargetID,Item5TargetID,Item6TargetID,Item7TargetID,Item8TargetID,Item9TargetID,PlaceName.Name,TerritoryType.PlaceName.Name,TerritoryType.PlaceNameRegion.Name,TerritoryType.Map.MapFilename,X,Z';

    fetch(urlFishing, {mode: 'cors'})
      .then(response => response.json())
      .then(results => results.Results.map(item => {
        fishingSpotData.push(item);
        return item;
      }));
    
    const gatheringPointData = [];
    const urlGathering = 'https://xivapi.com/GatheringPoint?limit=-1&columns=ID,GatheringPointBase.ID,GatheringPointBase.GatheringLevel,GatheringPointBase.GatheringType.ID,GatheringPointBase.GatheringType.Name,GatheringPointBase.GatheringType.IconMain,TerritoryType.Map.ID,TerritoryType.Map.PlaceNameRegion.Name,TerritoryType.Map.PlaceName.Name,GatheringPointBase.Item0.Item.ID,GatheringPointBase.Item1.Item.ID,GatheringPointBase.Item2.Item.ID,GatheringPointBase.Item3.Item.ID,GatheringPointBase.Item4.Item.ID,GatheringPointBase.Item5.Item.ID,GatheringPointBase.Item6.Item.ID,GatheringPointBase.Item7.Item.ID,PlaceName.Name,TerritoryType.Map.MapFilename';

    fetch(urlGathering, {mode: 'cors'})
      .then(response => response.json())
      .then(results => results.Results.map(item => {
          gatheringPointData.push(item);
          return item;
      }));

    this.setState({
      mapData: mapData,
      fishingSpotData: fishingSpotData,
      gatheringPointData: gatheringPointData,
      isLoading: false
    });
  }

  updateSearchTerm(searchTerm) {
    this.setState({searchString: searchTerm});
  }

  performSearch() {
    const url = `https://xivapi.com/search?string=${this.state.searchString.replace(' ', '%20')}`
    
    this.setState({
      gatheringItemResults: [],
      craftingItemResults: [],
      fishingItemResults: [],
      searchResults: []
    });

    fetch(url, {mode: 'cors'})
      .then(response => response.json())
      .then(result => {
        let items = result['Results'].map(item => {
          item.itemId = this.state.lastIndex;
          this.setState({lastIndex: this.state.lastIndex + 1});
          return item;
        });

        this.setState({
          searchResults: items
        });
      });
  }

  lookupItem(obj) {
    const url = `https://xivapi.com/item/${obj.ID}`

    fetch(url, {mode: 'cors'})
      .then(response => response.json())
      .then(result => {
        if(result.GameContentLinks.hasOwnProperty('GatheringItem')){
          this.setState({
            gatheringItemResults: [
              result.Name,
              result.ID,
              result.GameContentLinks.GatheringItem.Item[0],
              result.Icon
            ]
          });
        } else if(result.GameContentLinks.hasOwnProperty('FishingSpot')){
          this.setState({
            fishingItemResults: result
          });
        } else {
          this.setState({
            gatheringItemResults: [`${result.Name} cannot be gathered`],
            fishingItemResults: [`${result.Name} cannot be fished`]
          });
        }
      });
  }

  lookupRecipe(obj) {
    const url = `https://xivapi.com/recipe/${obj.ID}?columns=ID,Icon,RecipeLevelTable,AmountIngredient0,AmountIngredient1,AmountIngredient2,AmountIngredient3,AmountIngredient4,AmountIngredient5,AmountIngredient6,AmountIngredient7,AmountIngredient8,AmountIngredient9,AmountResult,ClassJob.Abbreviation,ClassJob.Icon,ItemIngredient0,ItemIngredient1,ItemIngredient2,ItemIngredient3,ItemIngredient4,ItemIngredient5,ItemIngredient6,ItemIngredient7,ItemIngredient8,ItemIngredient9,ItemIngredientRecipe0,ItemIngredientRecipe1,ItemIngredientRecipe2,ItemIngredientRecipe3,ItemIngredientRecipe4,ItemIngredientRecipe5,ItemIngredientRecipe6,ItemIngredientRecipe7,ItemIngredientRecipe8,ItemIngredientRecipe9,ItemResult`

    this.setState({
      selectedObjResults: [],
      craftingItemResults: [],
      fishingItemResults: []
    });

    fetch(url, {mode: 'cors'})
      .then(response => response.json())
      .then(result => {
        this.setState({
          craftingItemResults: result
        });
      });
  }

  updateSelectedObj(obj) {
    this.setState({
      selectedObj: obj,
      gatheringItemResults: [],
      craftingItemResults: [],
      fishingItemResults: []
    })

    if(obj['UrlType'] === 'Recipe') {
      this.lookupRecipe(obj);
    } else {
      this.lookupItem(obj);
    }
  }

  render () {
    if(this.state.isLoading) {
      return (
        <div>
          <img align='center' src="./Loading.gif" alt="Loading..." />
          <h2 align='center'>Loading...</h2>
        </div>
      )
    } else {
      return (
        <>
          <div className="app">
            <NavBar />
            <div id="page-body">
              <HomePage 
                updateSearchTerm={this.updateSearchTerm}
                updateSelectedObj={this.updateSelectedObj} 
                performSearch={this.performSearch} 
                searchString={this.state.searchString}
                searchResults={this.state.searchResults}
                selectedObj={this.state.selectedObj}
              />
              <CraftingPage
                obj={this.state.craftingItemResults}
                lookupItem={this.lookupItem}
              />
              <GatheringPage
                obj={this.state.gatheringItemResults}
                mapData={this.state.mapData}
                gatheringPointData={this.state.gatheringPointData}
              />
              <FishingPage 
                obj={this.state.fishingItemResults}
                fishingSpotData={this.state.fishingSpotData}
              />
            </div>
          </div>
        </>
      );
    }
  }
}

export default App;