import React, {Component} from 'react';

class FishingMap extends Component {
    render() {
        const multiplier = window.innerWidth > 420 ? 1 : 0.75;

        return (
            <div
                className={this.props.selectedRegion.includes(this.props.fishingSpots[0].TerritoryType.PlaceName.Name) ? '' : 'invisible'} 
                id="map-container">
                <img
                    id="map"
                    src={this.props.url}
                    alt="Map"
                />
                {this.props.fishingSpots.map((spot, key) => {
                    return (
                        <img
                            src='https://xivapi.com//cj/1/fisher.png'
                            alt="i"
                            style= {{
                                position: 'absolute',
                                width: 32 * multiplier,
                                height: 32 * multiplier,
                                top: (spot.Z / (window.innerWidth > 420 ? 2 : 5.12)) - (16 * multiplier),
                                left: (spot.X / (window.innerWidth > 420 ? 2 : 5.12)) - (16 * multiplier)
                            }}
                            key={key}
                        />
                    );
                })}
            </div>
        )
    }
}

export default FishingMap;