import React, {Component} from 'react';
import {FaAngleRight, FaAngleDown} from 'react-icons/fa';
import FishingMap from './FishingMap';

class FishingSpots extends Component {
    constructor() {
        super();
        this.state ={

        };
    }

    render() {
        const territories = [];
        const regions = [];

        for(let i = 0; i < this.props.fishingSpots.length; i++) {
            if(!territories.includes(this.props.fishingSpots[i].TerritoryType.PlaceNameRegion.Name)) {                
                territories.push(this.props.fishingSpots[i].TerritoryType.PlaceNameRegion.Name);
            }

            if(!regions.includes(this.props.fishingSpots[i].TerritoryType.PlaceName.Name)) {
                regions.push(this.props.fishingSpots[i].TerritoryType.PlaceName.Name);
            }
        }

        return(
            <>
                {territories.map((territory, key) => {
                if(territory !== null) {
                    return (
                        <React.Fragment key={key}>
                            <h2 onClick={this.props.toggleTerritories}>{this.props.selectedTerritory.includes(territory) ? <FaAngleDown /> : <FaAngleRight />} {territory} ({this.props.fishingSpots.filter(x => x.TerritoryType.PlaceNameRegion.Name === territory).length} nodes)</h2>
                            {regions.map((region, key) => {
                                if(this.props.fishingSpots.filter(x => x.TerritoryType.PlaceNameRegion.Name === territory && x.TerritoryType.PlaceName.Name === region).length > 0) {
                                    return (
                                        <div key={key} className={this.props.selectedTerritory.includes(territory) ? '' : 'invisible'}>
                                        <h3 onClick={this.props.toggleRegions}>{this.props.selectedRegion.includes(region) ? <FaAngleDown /> : <FaAngleRight />} {region}</h3>
                                            <FishingMap
                                                url={`https://xivapi.com${this.props.fishingSpots.filter(x => x.TerritoryType.PlaceName.Name === region)[0].TerritoryType.Map.MapFilename}`}
                                                fishingSpots={this.props.fishingSpots.filter(x => x.TerritoryType.PlaceName.Name === region)}
                                                selectedRegion={this.props.selectedRegion}
                                            />
                                        </div>
                                    )
                                } else {
                                    return <div className='invisible' key={key}/>
                                }
                            })}                                                
                        </React.Fragment>
                    );
                } else {
                    return (
                        <div key={key}/>
                    );
                }
            })}
            </>
        )
    }
}

export default FishingSpots;