import React, {Component} from 'react';
import Recipe from '../Components/Recipe';

class CraftingPage extends Component {
    constructor() {
        super();
        this.state = {
            itemToBeCrafted: [],
            ingredientAmounts: [],
            ingredients: []
        };
    }

    render() {
        if(this.props.obj.hasOwnProperty('AmountIngredient0')) {
            return (
                <>
                    <div>
                        <img align="right" src={`https://xivapi.com${this.props.obj.ItemResult.Icon}`} alt="Recipe item"/>
                        <h2>{this.props.obj.ItemResult.Name}</h2>
                    </div>
                    <Recipe 
                        recipeID={this.props.obj.ID}
                        lookupItem={this.props.lookupItem}
                    />
                </>
            )
        } else {
            return <div className="invisible" />
        }
    }
}

export default CraftingPage;